<template>
  <v-container grid-list-xl text-xs-center>
    <v-layout row fill-height fluid>
      <v-flex md8 offset-md2>
        <div class="dialog">
          <div class="messages" v-for="(msg, index) in messages" :key="index">
            <div v-if="msg.author" align="right">
              <div class="talk-bubble blue darken-4 white--text elevation-2">
                {{ msg.text }}
              </div>
            </div>
            
            <div v-else align="left">
              <div v-if="msg.template">
                <v-carousel hide-delimiters class="template">
                  <v-carousel-item v-for="(element, c) in msg.template.payload" :key="c">
                    <v-container>
                      <h3 class="subtitle">{{ element.title }}</h3>
                      <div>{{ element.subtitle }}</div>
                      <v-btn v-for="(button, b) in element.buttons" :key="b" flat color="white" class="primary darken-4" @click="startCourse(button.payload)">Start {{ button.payload}}</v-btn>
                    </v-container>    
                  </v-carousel-item>
                </v-carousel>
              </div>
              <div v-if="msg.quick_replies && hasOptions">
                <div class="text-xs-center quick-replies">
                  <v-chip v-for="(option, q) in msg.quick_replies" :key="q" color="primary" class="white--text elevation-2 px-2" @click="selectedOption(option.title)">
                    {{ option.title }}
                  </v-chip>
                </div>
              </div>
                <div v-if="msg.text">
                  <img 
                  src="img/icons/oso-icon-64x64.png"
                  height="20px"
                  class="bot"
                  />  
                <div class="talk-bubble white blue--text darken-4 elevation-2">
                  {{ msg.text }}
                </div>

              </div>
            </div>
          </div>
        </div>
    
        <v-bottom-nav :value="true" color="white" class="message-box" fixed>
          <v-container>
            <v-layout row wrap>
              <v-flex xs12 md12>
                <v-form @submit.prevent="sendMessage">
                  <v-text-field
                    v-model="message"
                    autocomplete="off"
                    focus
                    required
                    box
                    clearable
                    label="message"
                    type="text"
                    :append-outer-icon="message ? 'send' : 'send'"
                    @click:append-outer="sendMessage"
                  ></v-text-field>
                </v-form>
              </v-flex>
            </v-layout>
          </v-container>
        </v-bottom-nav>
      </v-flex>
    </v-layout>

  <div class="loader text-xs-center">
    <v-dialog
      v-model="dialog"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Please stand by
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
  </v-container>
</template>

<script>
import helper from '@/utils/helper.js'
import io from 'socket.io-client'

export default {
  data () {
    return {
      author: 'femi',
      message: '',
      messages: [],
      socket: io('https://osobot.herokuapp.com/'),
      //socket: io('http://localhost:5000'),
      dialog: false,
      incoming: null,
      hasOptions: false
    }
  },
  methods: {
    startCourse(courseCode){
      this.sendPostback(courseCode)
    },
    selectedOption(option){
      this.sendPostback(option)
    },
    sendMessage (e) {
      e.preventDefault()
      if (this.author && this.message) {
        let data = { author: this.author, text: this.message }
        this.socket.emit('send_message', data)
        this.clearMessage()
        this.addToChat(data)
      }
    },
    listenMessage (e) {
      e.preventDefault()
      helper.listen().then((result) => {
        if(result != undefined){
          this.addSpeech(e, result)
        }
      }).catch((error) => {
        console.log(error)
      });
    },
    sendPostback (textMessage) {
      if (this.author && textMessage) {
        let data = { author: this.author, text: textMessage }
        this.socket.emit('send_message', data)
        this.clearMessage()
        this.addToChat(data)
      }
    },
    clearMessage () {
      this.message = ''
    },
    addSpeech(e, data){
      this.message = data
      this.sendMessage(e)
    },
    addToChat(data){
      // console.log(data)
      if(data.quick_replies){
        this.hasOptions = true
      }
      else{
        this.hasOptions = false
      }
      this.messages = [...this.messages, data]
      localStorage.setItem("messages", JSON.stringify(this.messages))
      window.scrollTo(0, document.body.scrollHeight); 
    },
    listen(){
      this.socket.on('new_message', (data) => {
        helper.blop()
        this.dialog = false
        this.addToChat(data)
        // you can also do this.messages.push(data)
      })
    },
    loader(){
      this.socket.on('processing', (data) => {
      this.dialog = true
      this.incoming = data
      })
    }
  },
  mounted () {
    this.messages = JSON.parse(localStorage.getItem("messages")) || []
    this.listen()
    this.loader()
  },
  watch: {
    messages() {
      window.scrollTo(0, document.body.scrollHeight); 
    },
    dialog (val) {
      if (!val) return
      setTimeout(() => (this.dialog = false), 10000)
    }    
  }
}
</script>

<style scoped>
.dialog{
  /* height: 400px; */
  overflow: hidden;
  overflow-y: scroll;
  padding-bottom: 200px !important;
  background: url('/img/emo.gif') fixed no-repeat;
  background-size: cover;
  width: 100%;
}

.talk-bubble {
	margin: 5px !important;
  display: inline-block;
  position: relative;
  max-width: 240px;
	height: auto;
  /* box-shadow: 1px 1px 1px silver !important; */
  border-radius: 15px;
	-webkit-border-radius: 15px;
	-moz-border-radius: 15px;
  padding: 0.7em;
  padding-top: 0;
	text-align: left;
  line-height: 1em;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  white-space: pre-line;  
  animation: popup 0.5s;
}

.template{
  text-align: center !important;
  background: #ccc;
  border-radius: 10px;
  height: 170px !important;
	/* margin-left: 5px !important; */
}

.message-box{
  z-index: 1000 !important;
  height: 80px !important;
}

.quick-replies{
  text-align: center;
  position: fixed;
  z-index: 2000;
  bottom: 90px !important;
}

.bot{
  display: none;
  vertical-align: bottom;
  animation: popup 0.1s;
}

@keyframes popup {
  0%{
    transform: scale(0.1);
  }
  100%{
    transform: scale(1);
  }
}
</style>

