import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'
import Chat from './components/Chat'
import About from './components/About'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat
    }
  ]
})
