<template>
 <v-layout>
    <v-flex xs12 sm6 offset-sm3>
      <div class="wrap">
        <v-img
          class="primary--text"
          height="200px"
          src="/img/edu.jpg"
        >
          <p class="caption text-xs-left px-4 mt-4"> {{ caption }} </p>

        </v-img>

        <span class="extend-img white">
          <img 
            src="/img/emo.gif"
            class="emo"
          />                
        </span>

      </div>

      <div class="mt-4 pt-4 details">
        <h3 class="blue--text display-1 font-weight-regular text-capitalize mt-4"> {{title}} </h3>
        <p class="m-4 px-2">
          {{ description }}
        </p>
      </div>

      <div class="mt-2">
        <router-link to="/chat">
          <v-btn flat color="white" class="primary mx-4 p-4 elevation-8">Message</v-btn>
        </router-link>
      </div>
    </v-flex>

  </v-layout>
</template>

<script>
// import helper from '@/utils/helper.js'

export default {
  data () {
    return {
      title: 'Oso',
      caption: 'An investment in knowledge pays the best interest.',
      description: 'An intelligent virtual assistant that helps users with revision for test or exam.'
    }
  },
  methods: {
    typeWriter(txt, i){
      let speed = 1000
      if (i < txt.length) {
        this.description += txt.charAt(i)
        i++
        let tw = this.typeWriter(txt, i)
        setTimeout(function() {
          tw
        }, speed)
      }
    }
  },
  mounted () {
    // let i = 0
    // let txt = 'A revision assistant bot that helps users with revision for test or exam.'
    // this.typeWriter(txt, i)
  }
}
</script>

<style scoped>
.wrap {
    position: relative;
}

.extend-img{
  text-align: center !important;
  border-radius: 100%;
  width: 0 auto;
  display: inline-block;
  margin-top: -50px;
  margin-left: -75px;
  position:absolute;  
}
.emo{
  width: 150px;
  height: 150px;
  border-radius: 50%;
}
.details{
  z-index: 5000 !important;
  position: relative;
}

</style>

