
export default {
  speak (message) {
    var msg = new SpeechSynthesisUtterance(message)
    var voices = window.speechSynthesis.getVoices()
    msg.voice = voices[10]
    msg.voiceURI = 'native'
    msg.volume = 1
    msg.rate = 1.2
    msg.pitch = 1
    msg.lang = 'en-US'
    window.speechSynthesis.speak(msg)
  },
  pause () {
    window.speechSynthesis.pause()
  },
  resume () {
    window.speechSynthesis.resume()
  },
  stop () {
    window.speechSynthesis.stop()
  },
  blop () {
    let audio = new Audio('aud/blop.mp3')
    audio.play()
  },
  listen(){
    return new Promise(function(resolve, reject) {
      let SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    
      //plugging
      var recognition = new SpeechRecognition();
      //recognition.continuous = false;
      recognition.lang = 'en-US';
      recognition.interimResults = false;
      recognition.maxAlternatives = 1;
  
      recognition.start();
  
      var text = "";
      //handling result
      recognition.onresult = function(event){
        var last = event.results.length - 1;
        text = event.results[last][0].transcript;      
        resolve(text);
      };
  
      recognition.onspeechend = function(){
        recognition.stop();
        return text
      };
  
      //handling errors
      recognition.onnomatch = function(event){
        reject("No match error : " + event.error + ". Error message: " + event.message);
      };
  
      recognition.onerror = function(event){
        reject("Error occured in recognition: " + event.error + ". Error message: " + event.message);
      };  
     })
  }
}
